}

.store-button {
  width: 250px;
  font-size: 30px;
  margin-top: 10px;
  background: hotpink;
  border: none;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  text-transform: uppercase;
  padding: 20px 0;
  position: absolute;
  top: 18%;
  z-index: 2;
}

.store-button:hover {
  background: white;
  color: hotpink;
}

.image-circles-container {
  display: flex;
  margin-top: 50px;
  justify-content: space-around;
  width: 200px;
  position: absolute;
  top: 21%;
  z-index: 10;
}

.image-circles {
  width: 20px;
  height: 20px;
  background: white;
  border-radius: 50%;
  border: 2px solid white;
  cursor: pointer;
}

.image-circles-current {
  background: hotpink;
}

.imageout-enter {
  position: absolute;
  opacity: 0.01;
  /* transform: translateY(0%) scale(0.01); */
  z-index: 1;
}

.imageout-enter-active {
  opacity: 1;
  /* position: absolute; */
  /* transform: translateY(0%) scale(1); */
  -webkit-transition: all 1000ms ease-in-out;
  transition: all 1000ms ease-in-out;
}

.imageout-exit {
  opacity: 1;
  /* width: 100%; */
  /* transform: scale(1); */
}

.image-exit-active {
  opacity: 0;
  /* width: 100%; */
  /* transform: scale(4); */
  -webkit-transition: all 1000ms ease-in-out;
  transition: all 1000ms ease-in-out;
}

.image-text-out-enter {
  position: absolute;
  top: 0;
  left: 0;
  width: 90%;
  display: flex;
  justify-content: center;
}

.image-text-out-enter-active {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  -webkit-transition: all 1000ms ease-in-out;
  transition: all 1000ms ease-in-out;
  width: 90%;
  display: flex;
  justify-content: center;
}

.image-text-out-exit {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 90%;
  display: flex;
  justify-content: center;
}

.image-text-out-exit-active {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 90%;
  -webkit-transition: all 1000ms ease-in-out;
  transition: all 1000ms ease-in-out;
  display: flex;
  justify-content: center;
}

.home-images-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.home-image-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90vw;
  overflow: hidden;
  margin: 10px 0;
}

.home-image {
  width: 100%;
  height: auto;
}

.home-image-button {
  position: absolute;
  top: 50%;
  text-decoration: none;
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 5px;
  color: white;
  background: darkblue;
  text-transform: uppercase;
  opacity: 1;
  width: 250px;
  padding: 20px 0;
  text-align: center;
  cursor: pointer;
}

@-webkit-keyframes button-hover {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes button-hover {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media screen and (min-width: 900px) {
  .home-slide-images {
    height: 700px;
    object-fit: cover;
  }

  .images-text-container {
    top: 18%;
  }

  .store-button {
    top: 28%;
  }

  .image-circles-container {
    top: 32%;
  }

  .home-images-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }

  .home-image-container {
    width: 90%;
  }

  .home-image-button {
    opacity: 0;
  }

  .home-image:hover + .home-image-button {
    opacity: 1;
    -webkit-animation: button-hover 2s;
            animation: button-hover 2s;
  }

  .home-image-button:hover {
    background: white;
    color: darkblue;
    opacity: 1;
  }

  .home-image-container .home-image {
    -webkit-transition: -webkit-transform 0.5s ease;
    transition: -webkit-transform 0.5s ease;
    transition: transform 0.5s ease;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  }

  .home-image-container:hover .home-image {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

