.cart-sidebar-no-items {
  font-size: 36px;
  margin: 0 10px;
}

.footer-container {
  background: -webkit-gradient(linear, left top, right top, from(blue), color-stop(purple), to(hotpink));
  background: linear-gradient(to right, blue, purple, hotpink);
  color: white;
  font-size: 20px;
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.socials-container {
  font-size: 30px;
  margin-right: 20px;
}

.social {
  padding: 0 5px;
}

.app-main-container {
  margin: 0;
  width: 100%;
}

.content-container {
  display: flex;
  width: 100%;
}

.header-navigation-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: -webkit-gradient(linear, left top, right top, from(blue), color-stop(purple), to(hotpink));
  background: linear-gradient(to right, blue, purple, hotpink);
  color: white;
}

.header-navigation-container-non-mobile {
  justify-content: space-between;
  align-items: center;
  background: -webkit-gradient(linear, left top, right top, from(blue), color-stop(purple), to(hotpink));
  background: linear-gradient(to right, blue, purple, hotpink);
  color: white;
  display: none;
}

.home-links-container {
  list-style: none;
  display: flex;
  align-items: center;
  width: 50%;
  font-family: Arial, Helvetica, sans-serif;
}

.home-links-container li {
  margin: 0 20px;
  font-size: 36px;
}

.header-link {
  text-decoration: none;
  color: white;
}

.header-link:hover {
  color: black;
}

.app-cart-icon:hover {
  cursor: pointer;
  color: black;
}

.navigation-container {
  display: flex;
  align-items: center;
  font-size: 40px;
  margin-right: 40px;
  padding: 0 20px;
}

.cart-counter {
  width: 25px;
  height: 25px;
  background: darkblue;
  border-radius: 50%;
  position: relative;
  bottom: 20px;
  right: 10px;
  color: white;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.links:hover {
  color: blue;
}

@media screen and (min-width: 900px) {
  .header-navigation-container {
    display: none;
  }

  .header-navigation-container-non-mobile {
    display: flex;
  }
}

html {
  box-sizing: border-box;
  width: 100%;
}

body {
  padding: 0;
  box-sizing: border-box;
  font-family: "PT Mono", monospace;
  width: 100%;
  position: relative;
  min-height: 100%;
  padding-bottom: 6rem;
  margin: 0;
}

#root {
  width: 100%;
}

